<template>
  <div class="common-layout">
    <el-container>
      <el-aside style="width: 200px; min-height: 100vh; background-color: #001529;">
      
        <el-menu
          router
          background-color="#001529"
          text-color="rgba(255,255,255,0.65)"
          active-text-color="#fff"
          style="border: none;"
          :default-active="$route.path"
          @select="handleSelect"
        >
          <router-link to="/main">
            <el-menu-item>系统首页</el-menu-item>
          </router-link>
        

          <el-sub-menu :index="'content-management'" v-model="contentManagementOpen">
            <template #title>
              <el-icon>
                <message />
              </el-icon>活动管理
            </template>
            <el-menu-item-group>
              <el-menu-item index="/3-2">志愿者招募</el-menu-item>
              <el-menu-item index="/3-1">工作招聘</el-menu-item>
              <el-menu-item index="/3-3">失物招领</el-menu-item>
              <el-menu-item index="/3-4">其他事项</el-menu-item>
            </el-menu-item-group>
          </el-sub-menu>

          <el-sub-menu :index="'user-info'" v-model="userInfoOpen">
            <template #title>
              <el-icon>
                <message />
              </el-icon>居民信息
            </template>
            <el-menu-item-group>
              <el-menu-item index="/1-1">普通用户</el-menu-item>             
              <el-menu-item index="/1-2">管理员</el-menu-item>
            </el-menu-item-group>
          </el-sub-menu>

          <el-sub-menu :index="'message-management'" v-model="messageManagementOpen">
            <template #title>
              <el-icon>
                <message />
              </el-icon>消息管理
            </template>
            <el-menu-item-group>
              <el-menu-item index="/2-1">反馈消息管理</el-menu-item>
              <el-menu-item index="/2-2">紧急通知</el-menu-item>
              <el-menu-item index="/2-3">安全专栏管理</el-menu-item>
              <el-menu-item index="/2-4">活动管理</el-menu-item>
            </el-menu-item-group>
          </el-sub-menu>
          <el-menu-item index="/pub">发布信息</el-menu-item>
          <el-menu-item index="/setting">系统设置</el-menu-item>
        </el-menu>
      </el-aside>

      <el-container>
        <el-header style="display: flex; justify-content: space-between; align-items: center;">
          <div>后台管理</div>
          <el-dropdown>
            <span class="el-dropdown-link">
              欢迎{{ username }}！
              <el-icon class="el-icon--right">
                <ArrowDown />
              </el-icon>
            </span>
            <template #dropdown>
              <el-dropdown-menu>
                <router-link to="/1-3" style="text-decoration: none;">
                  <el-dropdown-item>个人中心</el-dropdown-item>
                </router-link>
                <el-dropdown-item @click="handleModifyPassword">修改密码</el-dropdown-item>
                <el-dropdown-item @click="logout">退出登录</el-dropdown-item>
              </el-dropdown-menu>
            </template>
          </el-dropdown>
        </el-header>

        <el-main>
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script setup>
import { ArrowDown } from '@element-plus/icons-vue';
import { ref } from 'vue';
import { useRouter } from 'vue-router';

const username = ref(localStorage.getItem('username') || '用户');
const router = useRouter(); 

// 控制子菜单的展开状态
const contentManagementOpen = ref(false);
const userInfoOpen = ref(false);
const messageManagementOpen = ref(false);

const handleSelect = (index) => {
  // 根据选择的菜单项来控制子菜单的展开状态
  if (index.startsWith('/3-')) {
    contentManagementOpen.value = true;
    userInfoOpen.value = false;
    messageManagementOpen.value = false;
  } else if (index.startsWith('/1-')) {
    userInfoOpen.value = true;
    contentManagementOpen.value = false;
    messageManagementOpen.value = false;
  } else if (index.startsWith('/2-')) {
    messageManagementOpen.value = true;
    contentManagementOpen.value = false;
    userInfoOpen.value = false;
  } else {
    contentManagementOpen.value = false;
    userInfoOpen.value = false;
    messageManagementOpen.value = false;
  }
};
const handleModifyPassword = () => {
  router.push("/pass");
};
const logout = () => {
  localStorage.clear(); 
  // 重定向到登录页面
  router.push('/login'); 
};
</script>




<style>
.el-menu-item,
.el-submenu__title {
  display: flex;
  align-items: center;
}
  .el-menu--inline {
    background-color: #000c17 !important;

  }
  .example-showcase .el-dropdown-link {
  cursor: pointer;
  color: var(--el-color-primary);
  display: flex;
  align-items: center;
}
  .el-menu--item {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .el-menu--item:hover {
    color: #fff !important;
  }

  .el-menu-item,
  .el-submenu__title {
    display: flex;
    align-items: center;
  }

  .el-menu-item .el-icon,
  .el-submenu__title .el-icon {
    margin-right: 10px;
  }

  .el-menu-item span,
  .el-submenu__title span {
    color: #fff;
    font-size: 16px;
  }

  .el-header {
    box-shadow: 2px 0 6px rgba(0, 21, 41, .35);
  }
</style>